import React from "react";
import { useAuth0 } from "@auth0/auth0-react";

const LoginButton = () => {
    const {loginWithRedirect, error, isLoading, isAuthenticated, logout } = useAuth0();

    if(isLoading){
        return <h2>Loading . . .</h2>
    }

    if(error){
        return <h2>Error: {error.message}</h2>
    }

    if(isAuthenticated){
        //Github
        return <button onClick={() => logout({returnTo: "https://fratiav.github.io/wishcar/"})}>Log Out</button>
    }else{
        return <button onClick={() => loginWithRedirect()}>Log In / Register</button>
    }
    
}

export default LoginButton;